import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Layout from '../layout/index.vue'

Vue.use(VueRouter)

export const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
  },
  {
      path: '/home',
      name: 'Home',
      component: Layout,
      redirect: '/home/index',
      children: [
        {
          path: 'index',
          name: 'HomeIndex',
          component: () => import('@/views/home/index'),
        }
      ]
  },
  {
    path: '/groups',
    name: 'Groups',
    component: Layout,
    redirect: '/groups/index',
    children: [
      {
        path: 'index',
        name: 'HomeIndex',
        component: () => import('@/views/groups/index'),
      }
    ]
  },
  {
    path: '/projects',
    name: 'Projects',
    component: Layout,
    redirect: '/projects/index',
    children: [
      {
        path: 'index',
        name: 'HomeIndex',
        component: () => import('@/views/projects/index'),
      }
    ]
  }
  

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
