import cookies from 'js-cookie'

function getToken() {
    let token = cookies.get('token')

    if (token) {
        return token
    }
    return ''
}
function getStudent() {
    let studentStr = cookies.get('student')
    
    if (studentStr) {
        return studentStr
    }
    return {}
}

export {
    getToken,
    getStudent
}