import { getToken, getStudent } from '@/utils/auth'
import cookies from 'js-cookie'

const state = {
    token: getToken(),
    student: getStudent(),
}

const mutations = {
    SET_TOKEN: (state, token) => {
        state.token = token
    },
    SET_ADMIN: (state, student) => {
        state.student = student
    }

}

const actions = {
    login({ commit }, token, student) {
        debugger
        commit('SET_TOKEN', token)
        commit('SET_ADMIN', student)
    },
    logout({ commit }) {
        commit('SET_TOKEN', '')
        commit('SET_ADMIN', {})
    }
}
  
  export default {
    namespaced: true,
    state,
    mutations,
    actions
  }